@import url(https://fonts.googleapis.com/css2?family=Lobster&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lora:wght@600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --header-text: "Lobster", "sans-serif";
  --header-text-color: #c689ee;
  --main-text: "Lora", "sans-serif";
  --light-green-color: #46f06b;
  --red-color: #8c0a0a;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.home-body {
  display: flex;
  flex: 1 1;
}

@media (max-width: 992px) {
  .home-body {
    display: flex;
    flex-direction: column;
  }
}

.header {
  display: flex;
  justify-content: space-around;
  border-bottom: 1px solid lightgray;
}
.logo > img {
  object-fit: fill;
  cursor: pointer;
}
.header-info {
  display: flex;
  align-items: center;
}
.header-info > .MuiAvatar-root {
  cursor: pointer;
}
.header-info > h4 {
  padding-left: 15px;
  cursor: pointer;
  color: #000;
  font-size: 20px;
  text-transform: uppercase;
  font-family: var(--header-text);
  font-weight: 400;
}
.header-info > .MuiSvgIcon-root {
  margin-left: 15px;
  cursor: pointer;
}
.header-info > h4:hover {
  color: var(--header-text-color);
}
.header-icons {
  display: flex;
  align-items: center;
}
.red {
  color: red;
}

.transparent {
  color: #000;
}

.mainOption {
  display: flex;
  flex-direction: column;
}

.top-info {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-bottom: 10px;
}
.top-info > h5 {
  padding-left: 10px;
  font-family: "Lora", "sans-serif";
  font-weight: 600;
  padding-top: 5px;
  color: #000;
}

.bottom-img {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.bottom-img > img {
  border-radius: 8px;
  width: 475px;
  max-width: 475px;
  height: 550px;
  max-height: 575px;
}

.image-section {
  display: flex;
  flex-direction: column;
  border: 1px solid lightgray;
  margin: 10px 0;
  border-radius: 8px;
}
.image-info > img {
  width: 475px;
  max-width: 475px;
  height: 550px;
  max-height: 575px;
}
.person-info {
  display: flex;
  padding: 10px 6px;
  align-items: center;
}
.person-info > h5 {
  padding-left: 10px;
  font-family: "Lora", "sans-serif";
  font-weight: 600;
  color: #000;
}
.caption-section {
  display: flex;
  align-items: center;
  padding-bottom: 5px;
}
.caption-section > .MuiSvgIcon-root {
  color: var(--header-text-color);
}
.caption-text {
  padding-left: 10px;
  font-family: "Lora", "sans-serif";
  font-weight: 600;
  color: #000;
  font-size: 14px;
}
.icons {
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding-top: 10px;
}
.like {
  display: flex;
  align-items: center;
}
.count-red {
  color: red;
  display: flex;
  align-items: center;
}
.icons > button {
  border: none;
  color: gray;
  background: transparent;
}
.icons > .MuiSvgIcon-root {
  color: gray;
  font-size: 28px;
}
.icons > button > span > .MuiSvgIcon-root {
  font-size: 28px;
}
.comments {
  display: none;
}
.comments-section {
  padding-top: 10px;
}
.comments-messages {
  display: flex;
  align-items: center;
}
.comments-messages > form > input {
  background: transparent;
}
.comments-messages > h5 {
  color: gray;
  font-weight: bold;
}
.comment-input {
  border: none;
  margin-left: 15px;
  outline: none;
  padding: 6px 0;
  font-family: var(--main-text);
  color: gray;
  font-size: 14px;
}
.comments-messages > form > button {
  display: none;
}
.messages-section {
  padding-bottom: 15px;
}

.messages {
  display: flex;
  align-items: center;
  color: gray;
  width: 425px;
  max-width: 425px;
}

.messages > p,
span {
  font-size: 14px;
  padding-left: 6px;
  font-family: var(--main-text);
}
.messages > p {
  color: var(--header-text-color);
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}
.messages > h5 {
  font-weight: bold;
}
.edit {
  display: none;
}
.messages > form > input {
  background-color: transparent;
}

.imageUpload {
  display: flex;
  flex-direction: column;
  max-width: 425px;
  justify-content: center;
  margin: 0 auto;
}

.imageUpload-progress {
  background: var(--light-green-color);
  color: var(--light-green-color);
  padding: 10px;
  margin: 10px auto;
  border-radius: 6px;
}
.imageUpload > p {
  padding-left: 10px;
  font-family: "Lora", "sans-serif";
  font-weight: 600;
  font-size: 14px;
  color: #000;
}
.imageUpload > input {
  border-radius: 6px;
  outline: 0;
  border: 1px solid gray;
  padding: 15px;
  background-color: var(--header-text-color);
  margin: 10px 0;
  cursor: pointer;
}
.btn-upload {
  padding: 15px 10px;
  font-family: "Lora", "sans-serif";
  font-size: 16px;
  font-weight: 600;
  color: #fff;
  background-color: var(--header-text-color);
  outline: 0;
  border: 1px solid transparent;
  border-radius: 6px;
  cursor: pointer;
}

.main {
  display: flex;
  flex: 0.6 1;
  flex-direction: column;
  padding-top: 55px;
  justify-content: center;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  background-color: whitesmoke;
  border: 1px solid lightgray;
  border-top: none;
  border-right: none;
}

.hero {
  display: flex;
  justify-content: center;
  padding-top: 25px;
}
.hero-profiles > img {
  height: 80px;
  border-radius: 50%;
  border: 1px solid var(--header-text-color);
  outline: 4px solid lightgray;
  width: 90px;
}
.hero-profiles:hover > img {
  border: none;
}
.hero-profiles {
  display: flex;
  flex-direction: column;
  padding-left: 15px;
  cursor: pointer;
  position: relative;
  align-items: center;
}
.hero-profiles:hover .MuiSvgIcon-root {
  color: var(--header-text-color);
}
.hero-plus {
  position: absolute;
  bottom: 12px;
  right: 5px;
  color: #055fd0;
  z-index: 1;
}
.back-image {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  z-index: 2;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
}
.back-image > img {
  display: grid;
  place-items: center;
  width: 25%;
  height: 35%;
  margin: 60px auto;
  box-shadow: 3px 5px 7px rgba(0, 0, 0, 0.5);
  border: 3px solid white;
}

.login {
  display: grid;
  place-items: center;
  height: 75vh;
}
.login > img {
  height: 255px;
}
.login > form {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: lightgray;
  border-radius: 6px;
  padding: 65px;
}
.login > form > input {
  border: 1px solid var(--header-text-color);
  outline: none;
  margin: 8px 0;
  cursor: pointer;
  border-radius: 8px;
  padding: 15px 35px;
}
.login > form > button {
  margin-top: 10px;
  width: 100%;
  background-color: var(--header-text-color);
  border: none;
  outline: none;
  font-size: 24px;
  text-align: center;
  text-transform: capitalize;
  color: #fff;
  padding: 10px 0;
  border-radius: 8px;
  cursor: pointer;
}
.login > form > button:hover {
  opacity: 0.7;
}
.register {
  color: var(--header-text-color);
  font-family: var(--main-text);
  font-weight: 600;
  font-size: 16px;
  cursor: pointer;
}

.forum-section {
  display: flex;
  flex-direction: column;
  flex: 0.4 1;
  padding-bottom: 25px;
  border: 1px solid lightgray;
  border-right: none;
  border-top: none;
}
.forum-header > h2 {
  color: var(--header-text-color);
  font-size: var(--main-text);
  font-weight: 600;
  text-align: center;
  padding-bottom: 10px;
  font-size: 28px;
}
.forum-input > form {
  display: flex;
  justify-content: center;
}
.forum-input > form > input {
  border: none;
  background-color: lightgray;
  padding: 12px;

  width: 250px;
  border-radius: 10px;
  outline: none;
}
.btn-forum {
  padding: 6px 10px;
  font-family: "Lora", "sans-serif";
  font-size: 16px;
  margin-left: 10px;
  font-weight: 600;
  color: #fff;
  cursor: pointer;
  background-color: var(--header-text-color);
  outline: 0;
  border: 1px solid transparent;
  border-radius: 6px;
}

.forumOption {
  display: flex;
  flex-direction: column;
  border-bottom: 1px solid gray;
}
.forumOption-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.forumOption-header > h5 {
  font-size: var(--main-text);
  color: var(--header-text-color);
  font-weight: 600;
  text-align: center;
  font-size: 18px;
  padding-left: 5px;
}
.forumOption-posts > form {
  display: flex;
  justify-content: space-between;
  padding-left: 5px;
}
.forumOption-posts > form > p {
  font-size: var(--main-text);
  font-weight: 600;
  color: gray;
  font-size: 14px;
  max-width: 220px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}
.time {
  display: flex;
  align-items: center;
}
.edit-input {
  border: 1px solid lightgray;
  border-radius: 8px;
  outline: none;
}

